@tailwind base;
@tailwind components;
@tailwind utilities;

.navbar {
  @apply bg-opacity-70;
  backdrop-filter: blur(5px);
  box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0.1);
  z-index: 99999;
}
.font-name1 {
  font-family: "Titan One", sans-serif;
  font-weight: 400;
  font-style: bold;
}
.font-name2 {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-style: normal;
}

.font-size1 {
  @apply text-4xl;
  @apply font-semibold;
}

.font-size2 {
  @apply font-thin;
}
.font-size3 {
  @apply text-2xl;
  @apply font-semibold;
}

.wave {
  padding-top: 2rem;
  background-color: aquamarine;
}

.navbar2 {
  @apply bg-opacity-70;
  backdrop-filter: blur(5px);
  box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0);
}

.bg-navbar {
  background-color: black;
}
